import React, { useEffect, useState } from 'react';
import Page from 'components/LayoutComponents/Page';
import { requiredRule } from 'utils/validateRules';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Card, Form, Row, Col, Input, message } from 'antd';
import { getQueryParams } from 'utils/urlUtil';
import { addTestSession, updateTestSession, getTestSessionById } from 'services/testSession';

const TestSessionForm = props => {
  const { form } = props;
  const { getFieldDecorator } = form;
  const [initialValue, setInitialValue] = useState({});
  const [loading, setLoading] = useState(false);

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 5 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 12 },
    },
    colon: false,
  };

  const handleInitialvalue = async () => {
    const { id } = getQueryParams();
    if (id) {
      const res = await getTestSessionById(id);
      setInitialValue(res.data);
    }
  };

  useEffect(() => {
    handleInitialvalue();
  }, []);

  const handleSubmit = e => {
    e.preventDefault();
    props.form.validateFields(async (err, values) => {
      if (!err) {
        setLoading(true);
        try {
          const { id } = initialValue;
          if (!id) {
            await addTestSession(values);
            message.success('Tạo ca thi thành công');
          } else {
            await updateTestSession({ id, ...values });
            message.success('Cập nhật ca thi thành công');
          }
          setTimeout(() => props.history.goBack(), 1500);
        } catch (error) {
          message.error('Có lỗi xãy ra');
        }
        setLoading(false);
      }
    });
  };

  return (
    <Page {...props}>
      <Card>
        <Row>
          <Col span={24}>
            <Form onSubmit={handleSubmit}>
              <Form.Item label="Tên" {...formItemLayout}>
                {getFieldDecorator('label', {
                  rules: [requiredRule],
                  initialValue: initialValue.label,
                })(<Input />)}
              </Form.Item>
              <Form.Item {...formItemLayout} label=" ">
                <BtnSave htmlType="submit" loading={loading} />
                <BtnCancel onClick={() => props.history.goBack()} loading={loading} />
              </Form.Item>
            </Form>
          </Col>
        </Row>
      </Card>
    </Page>
  );
};

export default Form.create()(TestSessionForm);



