import { request } from 'utils';

const prefix = '/api/education';

export const getTestSessions = ({ page = 1, isPagingEnabled = true } = {}) =>
  request({
    prefix,
    url: '/TestSession',
    params: {
      page,
      isPagingEnabled,
    },
    method: 'GET',
  });

export const getTestSessionById = id =>
  request({
    prefix,
    url: `/TestSession/${id}`,
    method: 'GET',
  });

export const deleteTestSession = id =>
  request({
    prefix,
    url: `/TestSession/${id}`,
    method: 'DELETE',
  });

export const addTestSession = ({ label }) =>
  request({
    prefix,
    url: '/TestSession',
    method: 'POST',
    data: { label },
  });

export const updateTestSession = ({ id, label }) =>
  request({
    prefix,
    url: `/TestSession/${id}`,
    method: 'PUT',
    data: { id, label },
  });

export const getTestSession = ({ label, page, pageSize = 10, orderBy, isPagingEnabled = true }) =>
  request({
    prefix,
    url: '/TestSession',
    method: 'GET',
    params: {
      label,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });



